body {
  margin: 0;
  padding: 0;
  font-family: Franklin Gothic Medium;
}

header {
  color: orange;
  text-align: center;
  background-color: #000;
  font-family: Times New Roman;
  font-size: 40px;
}

.btncontainer {
  justify-content: center;
  gap: 30px;
  margin: 0;
  display: flex;
}

#loadArtsBtn, #loadNewsBtn, #loadSportsBtn {
  color: #fff;
  cursor: pointer;
  background-color: #06c;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 30px;
  transition: background-color .3s;
}

#loadArtsBtn:hover, #loadNewsBtn:hover, #loadSportsBtn:hover {
  background-color: orange;
}

.news-sections {
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 2900px;
  margin: 2rem auto;
  display: flex;
}

article {
  box-sizing: border-box;
  border-radius: 10px;
  flex: 30%;
  margin: 2rem;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 0 10px #fff3;
}

article:hover {
  transform: translateY(-50px);
  box-shadow: 0 0 20px #ffffffb3;
}

@media (width <= 768px) {
  article {
    flex: 100%;
  }
}

article img {
  border-radius: 10px;
  width: 100%;
  height: auto;
}

article h4 {
  color: #fff;
  margin-top: 1rem;
  font-size: 1.5rem;
}

article p {
  color: #f0f8ff;
  margin-top: .5rem;
  font-size: 2rem;
}

article a {
  color: orange;
  margin-top: .5rem;
  font-size: 40px;
  text-decoration: none;
  display: inline-block;
}

article a:hover {
  text-decoration: underline;
}

footer {
  text-align: center;
  color: #f0f8ff;
  margin-top: 2rem;
  font-size: 50px;
}
